.nhance-app {
  background: rgb(21, 29, 45);
  padding: 25px 35px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  bottom: 0;
  width: 100%;
  left: 0;
  &__close {
    color: $white;
    font-size: 11px; }
  &__logo {
    width: 71px;
    height: 71px;
    border-radius: 15px;
    background-color: $white;
    @include center;
    img {
      width: 33px; } }
  &__text {
    h3 {
      font-size: 21px;
      font-weight: bold;
      color: $pink-red;
      margin-bottom: 10px;
      line-height: 1; }
    p {
      font-size: 14px;
      color: $brown-grey; } } }


.blocker {
  background-color: rgba($very-light-pink-two, 0.9); }
.modal a.close-modal {
  display: none; }
.verify-otp {
  padding: 0;
  width: 495px;
  border-radius: 15px;
  box-shadow: 0px 1px 3px 0 rgba(0, 0, 0, 0.2);
  background-color: $white;
  display: block;
  overflow: hidden;
  display: none;
  &__title {
    padding: 50px 0; }
  &__input {
    padding: 0 55px;
    position: relative;
    input {
      width: 100%;
      font-size: 21px;
      font-weight: normal;
      color: $brown-grey;
      letter-spacing: 63px;
      background: none;
      padding: 19px 0; }
    &__decor {
      display: flex;
      justify-content: space-between;
      width: calc(100% - 60px);
      position: absolute;
      top: 0;
      left: 30px;
      z-index: -1;
      span {
        width: 60px;
        height: 60px;
        border-radius: 10px;
        border: solid 1px #bdbdbd;
        background-color: $white-three;
        display: block; } } }
  p, strong {
    font-size: 18px;
    font-weight: normal;
    text-align: center;
    color: $dark;
    display: block; }
  strong {
    font-weight: 600; }
  .btn-resend-otp {
    @include center;
    font-size: 18px;
    font-weight: 600;
    color: $dark;
    margin-top: 50px;
    i {
      font-size: 15px;
      color: $dark;
      position: relative;
      top: -2px;
      font-weight: 900;
      margin-left: 15px; } }
  .btn-verify-otp {
    width: 100%;
    @include center;
    justify-content: flex-end;
    padding: 28px 65px;
    font-size: 18px;
    font-weight: bold;
    background: $white-three;
    color: $dark;
    text-transform: uppercase;
    border-top: 1px solid $very-light-pink-three;
    margin-top: 50px; } }
